import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import { useEffect, useState } from "react";
import NavMenu from "./NavMenu";
import { Icon } from "@iconify/react";
import { useRouter } from "next/router";
import NavLink from "@/components/NavLink";
import Image from "next/image";
import { styled } from "@mui/material/styles";
import { BASE_PATH } from "@/globals";
interface Props {
  darkMode: boolean;
  switchDarkMode: () => void;
}

// Styled component to make the link look like a button
const StyledIconButtonLink = styled("a")({
  color: "inherit",
  display: "inline-flex",
  alignItems: "center",
  justifyContent: "center",
  textDecoration: "none",
  // Remove underline from link
  "&:hover": {
    cursor: "pointer" // Change cursor on hover
  }
});
export default function NavBar({
  darkMode,
  switchDarkMode
}: Props) {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const router = useRouter();
  useEffect(() => {
    setDrawerOpen(false);
  }, [router.pathname]);
  return <Box sx={{
    flexGrow: 1,
    display: "flex"
  }} data-sentry-element="Box" data-sentry-component="NavBar" data-sentry-source-file="NavBar.tsx">
      <AppBar position="static" sx={{
      zIndex: theme => theme.zIndex.drawer + 1,
      backgroundColor: darkMode ? "#19191c" : "white",
      color: darkMode ? "white" : "black"
    }} enableColorOnDark data-sentry-element="AppBar" data-sentry-source-file="NavBar.tsx">
        <Toolbar variant="dense" data-sentry-element="Toolbar" data-sentry-source-file="NavBar.tsx">
          <IconButton size="large" edge="start" color="inherit" aria-label="menu" sx={{
          mr: "min(0.5vw, 0.6rem)",
          padding: 1,
          my: 1
        }} onClick={() => setDrawerOpen(val => !val)} data-sentry-element="IconButton" data-sentry-source-file="NavBar.tsx">
            <Icon icon="mdi:menu" data-sentry-element="Icon" data-sentry-source-file="NavBar.tsx" />
          </IconButton>

          <Image src={`${BASE_PATH}/favicon-32x32.png`} alt="DA3DALU5 SYSTEMS LOGO" width={32} height={32} data-sentry-element="Image" data-sentry-source-file="NavBar.tsx" />

          <NavLink href="/" data-sentry-element="NavLink" data-sentry-source-file="NavBar.tsx">
            <Typography variant="h6" component="div" sx={{
            flexGrow: 1,
            ml: 1,
            fontSize: {
              xs: "1rem",
              sm: "1.25rem"
            }
          }} data-sentry-element="Typography" data-sentry-source-file="NavBar.tsx">
              CHONSE
            </Typography>
          </NavLink>

          <StyledIconButtonLink href="https://github.com/ICARUS-2/chonse" target="_blank" rel="noopener noreferrer" sx={{
          ml: "min(0.6rem, 0.8vw)"
        }} data-sentry-element="StyledIconButtonLink" data-sentry-source-file="NavBar.tsx">
            <IconButton color="inherit" component="span" data-sentry-element="IconButton" data-sentry-source-file="NavBar.tsx">
              <Icon icon="mdi:github" data-sentry-element="Icon" data-sentry-source-file="NavBar.tsx" />
            </IconButton>
          </StyledIconButtonLink>

          <IconButton sx={{
          ml: "min(0.6rem, 0.8vw)"
        }} onClick={switchDarkMode} color="inherit" edge="end" data-sentry-element="IconButton" data-sentry-source-file="NavBar.tsx">
            {darkMode ? <Icon icon="mdi:brightness-7" /> : <Icon icon="mdi:brightness-4" />}
          </IconButton>
        </Toolbar>
      </AppBar>
      <NavMenu open={drawerOpen} onClose={() => setDrawerOpen(false)} data-sentry-element="NavMenu" data-sentry-source-file="NavBar.tsx" />
    </Box>;
}